
































export interface Data {
  appKey: string,
  appSecret: string,
}

import Vue from 'vue'
export default Vue.extend({
  data: () => ({
    appKey: '',
    appSecret: '',
  }),
  methods: {
    submitForm(event: Event) {
      event.preventDefault();

      this.$emit('login', {
        appKey: this.appKey,
        appSecret: this.appSecret,
      } as Data);
    }
  }
})
